<template>
    <form class="card auth-card" @submit.prevent="onSubmit">
        <div class="card-content">
          <span class="card-title">{{localization('Home bookkeeping')}}</span>
          <div class="input-field">
            <input
                :class="{invalid:$v.email.$dirty && $v.email.$error}" 
                id="email"
                type="text"
                v-model="email"
            >
            <label for="email">{{localization('Email')}}</label>
            <div v-if="$v.email.$dirty && !$v.email.required" class="invalid-feedback" style="min-height: 18px;display: block;font-size: 12px;color: #f44336">
                {{localization('Invalid_email_1')}}
            </div>
            <div v-if="$v.email.$dirty && !$v.email.email" class="invalid-feedback" style="min-height: 18px;display: block;font-size: 12px;color: #f44336">
                  {{localization('Invalid_email_2')}}
            </div>
          </div>
          <div class="input-field">
            <input
                :class="{invalid:$v.password.$dirty && $v.password.$error}" 
                id="password"
                type="password"
                v-model="password"
            >
            <label for="password">{{localization('Password')}}</label>
            <div v-if="$v.password.$dirty && !$v.password.required" class="invalid-feedback" style="min-height: 18px;display: block;font-size: 12px;color: #f44336">
                {{localization('Enter password')}}
            </div>
            <div v-if="$v.password.$dirty && !$v.password.minLength" class="invalid-feedback" style="min-height: 18px;display: block;font-size: 12px;color: #f44336">
                  {{localization('Invalid_Password_Long')}}
            </div>
          </div>
          <div class="input-field">
            <input
                :class="{invalid:$v.name.$dirty && $v.name.$error}" 
                id="name"
                type="text"
                v-model="name"
            >
            <div v-if="$v.name.$dirty && !$v.name.required" class="invalid-feedback" style="min-height: 18px;display: block;font-size: 12px;color: #f44336">
                {{localization('Enter name')}}
            </div>
            <label for="name">{{localization('Name')}}</label>

          </div>
        <div class="switch" style="margin-bottom: 2rem;">
          <label>
            English
            <input type="checkbox" v-model="isRuLocale">
            <span class="lever"></span>
            Русский
          </label>
        </div>
          
        </div>
        <div class="card-action">
          <div>
            <button
                class="btn waves-effect waves-light auth-submit"
                type="submit"
            >
              {{localization('Register')}}
              <i class="material-icons right">send</i>
            </button>
          </div>
      
          <p class="center">
            {{localization('Already have an account')}}
            <router-link to="/login">{{localization('Press here')}}</router-link>
          </p>
        </div>
    </form>
</template>

<script>
import { required, email, minLength } from 'vuelidate/lib/validators'
import ru from '../locales/ru.json'
import en from '../locales/en.json'


export default {
  data() {
    return {
      email: '',
      password: '',
      name: '',
      isRuLocale: true
    }
  },
  metaInfo: {
    title: 'Registration | CRM' // тайтл во вкладке
  },
  validations: {
      email: { required, email },
      password: { required, minLength: minLength(6) },
      name: { required }
  },
  methods: {
    async onSubmit() {
      if (this.$v.$invalid) {
          this.$v.$touch()
          return
        }
      const formData = {
        email: this.email,
        password: this.password,
        name: this.name,
        locale: this.isRuLocale ? 'ru-RU' : 'en-US'
      }
      try {
        await this.$store.dispatch('register', formData)
        this.$router.push('/')
      } catch(e) {}
    },
    localization(key) {
      const locales = {
        'ru-RU': ru,
        'en-US': en
      }
      let locale = null
      if (this.isRuLocale) {
        locale = 'ru-RU'
      } else {
        locale = 'en-US'
      }
      return locales[locale][key]|| ['Localize Error']
    }
  }
}
</script>